export default {
	 name:'Select',
	 props: {
		  placeholderStyle:{
			type: String,
			default:'color:#F76260'
		  },
		  activeIndex:{
			 type:Number,
			 default:2 
		  },
		  list:{
			 type:Array,
			 default(){
				return []
			} 
		  },
		},
		data() {
			return {
			   isVal:'请选择',
			   showMask:false,
			   datas:{},
			   defaultVal:[],
			}
		},
		onLoad(e) {
			
		},
		created() {
	    
	    },
		methods: {
			onClick(){
				this.showMask=true
			},
			onConfirm(){ //确定
				this.showMask=false
			},
			onCancel(){ //取消
				this.showMask=false
			},
			bindChange(val){ //获取数据
				let arr=val.detail.value;
				    console.log(arr)
				this.defaultVal=arr;
			},
			convertListData(orginArr){ //转换列表的数据格式
				const arr = [];
				orginArr.forEach((val, index)=>{
					let value = typeof val === 'object' && 'value' in val ? val.value : val;
					let isDisabled = typeof val === 'object' && val.disabled == true;
					arr.push({
						isActive: false,
						value: value,
						disabled: isDisabled,
					});
				});
				return arr;
			},
		},
		 computed: {
			innerList(){
				return this.convertListData(this.list);
			},
        }
	}
